async function Init() {
    const response = await fetch(scotchFile);
    const file = await response.text();

    Papa.parse(file, {
        header: true,
        worker: true,
        dynamicTyping: true,
        complete: function(results) {
            console.log(results);
            processResults(results);
        }
    })
}

function processResults(results:any) {
    for (var i=0; i<WhiskysList.length; i++) {
        let regex = new RegExp(WhiskysList[i][0].toString(), "i");
        let newArray = results.filter((x: { [x: string]: string; }) => x["Whisky Name"].match(regex));
        console.log(newArray);
    }
}

Init();